/** Media Queries **/
html {
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Oswald", sans-serif;
  font-size: 1.6vw;
  line-height: 1.8;
  background-color: #0d1511;
  color: #f0f1f1;
  overflow: hidden;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    display: flex;
  }
  body aside {
    float: left;
    width: 195px;
    height: 100%;
  }
}

p {
  color: #f0f1f1;
  font-size: 3rem;
}
@media (min-width: 1024px) {
  p {
    font-size: 1.5vw;
  }
}

a {
  text-decoration: none;
  cursor: pointer;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Oswald", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 11.5rem;
  color: #93d5b5;
  text-align: left;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 5.5vw;
  }
}

h2 {
  font-size: 5rem;
  margin-bottom: 0;
}
@media (min-width: 1024px) {
  h2 {
    font-size: 3vw;
  }
}

h3 {
  font-size: 3.5rem;
}
@media (min-width: 1024px) {
  h3 {
    font-size: 2.5vw;
  }
}

h4 {
  font-size: 3rem;
}
@media (min-width: 1024px) {
  h4 {
    font-size: 2vw;
  }
}

.box .navegacion {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 100%;
  height: auto;
  margin-bottom: 2rem;
  background-color: #0d1511;
  z-index: 5;
}
.box .navegacion h4 {
  display: none;
}
.box .navegacion .header {
  display: flex;
  flex-direction: column;
}
.box .navegacion .header .menu {
  cursor: pointer;
  display: flex;
  justify-content: center;
}
.box .navegacion .header .menu .abrir-menu {
  display: block;
}
.box .navegacion .header .menu .cerrar-menu {
  display: none;
}
.box .navegacion .opciones {
  display: none;
}
.box .navegacion .opciones .enlaces {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.box .iconos {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
  padding: 0 2rem;
  transition: filter 0.3s ease-in-out;
}
.box .iconos a {
  padding: 0;
}
.box .iconos .linkedin {
  margin-bottom: 2rem;
}

@media (min-width: 1024px) {
  .box {
    width: 30vw;
    height: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-left: 2rem;
  }
  .box .navegacion {
    width: auto;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    z-index: 1;
  }
  .box .navegacion h4 {
    color: #93d5b5;
    display: flex;
    margin-top: 0rem;
    text-align: center;
  }
  .box .navegacion .header .menu {
    display: none;
  }
  .box .navegacion .opciones {
    display: contents;
  }
  .box .navegacion .opciones .enlaces {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    flex: auto;
  }
  .box .iconos {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
    padding: 0 2rem;
  }
  .box .iconos a {
    padding: 0;
  }
  .box .iconos .linkedin {
    margin-bottom: 2rem;
  }
}
button {
  all: unset;
}

.boton {
  width: 100%;
  text-align: center;
  color: #f0f1f1;
  padding: 1rem 0;
  cursor: pointer;
  border: 1px solid #93d5b5;
  border-radius: 2rem;
  transition: filter 0.3s ease-in-out;
}

.neon {
  transition: filter 0.3s ease-in-out;
  z-index: 4;
}
.neon:hover {
  filter: drop-shadow(0px 0px 30px #18e985);
  z-index: 4;
}

.secciones {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: scroll;
}
@media (min-width: 1024px) {
  .secciones {
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
  }
}
.secciones::-webkit-scrollbar {
  display: none;
}
.secciones section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: normal;
  scroll-snap-align: start;
}
.secciones section:first-child {
  padding-top: 20rem;
}
@media (min-width: 1024px) {
  .secciones section {
    height: 100vh;
  }
}
@media (min-width: 768px) {
  .secciones section:first-child, .secciones section:last-child {
    padding-top: 2rem;
  }
}
@media (min-width: 768px) {
  .secciones section:first-child {
    margin-top: 20rem;
  }
}
.secciones section .titulo {
  border-bottom: 1px solid #93d5b5;
}
.secciones section .about {
  display: flex;
  flex-direction: column;
  margin: 0 2rem;
}
@media (min-width: 1024px) {
  .secciones section .about {
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 20px;
    text-align: left;
  }
}
.secciones section .about .intro {
  display: flex;
  flex-direction: column;
}
.secciones section .about .intro h1 {
  margin: 0 0 6rem 0;
}
.secciones section .about .intro p {
  margin: 0;
}
.secciones section .about .intereses {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.secciones section .about .intereses .saber {
  display: none;
}
@media (min-width: 1024px) {
  .secciones section .about .intereses .saber {
    display: block;
  }
}
.secciones section .about .intereses h4 {
  border-bottom: 1px solid #93d5b5;
  padding: 0 3rem;
}

.block {
  display: block;
}

.none {
  display: none;
}

.parpadeo {
  filter: drop-shadow(0px 0px 1rem #18e985);
  animation: parpadeo 2s infinite alternate ease-in-out;
}

@keyframes parpadeo {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.7;
  }
}
.proyectos {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: clip;
}
.proyectos .contenedor {
  margin: 2rem;
  margin-bottom: 6rem;
  width: 80vw;
  height: 30rem;
  background-color: #93d5b5;
  text-align: center;
  justify-self: center;
  align-items: center;
  cursor: pointer;
  transition: all 100s;
  z-index: 3;
}
.proyectos .contenedor p {
  opacity: 0;
  margin: 0 2rem;
}
.proyectos .contenedor p span {
  color: #18e985;
}
.proyectos .contenedor:hover {
  background-color: white;
  background-image: none;
  z-index: 1;
}
.proyectos .contenedor:hover p {
  opacity: 1;
  color: black;
}

@media (min-width: 768px) {
  .proyectos {
    display: flex;
  }
  .proyectos .contenedor {
    min-width: 70%;
    max-width: 30rem;
    margin: 2rem auto; /* Centra el div horizontalmente */
    background-color: #93d5b5;
    text-align: center;
    justify-self: center;
    align-items: center;
    transition: background-color 500ms;
    cursor: pointer;
  }
}
@media (min-width: 1024px) {
  .proyectos {
    height: 100vh;
    margin: 2rem;
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .proyectos .contenedor {
    min-width: 90%;
    min-height: 50%;
    height: 25rem;
    margin: 0 auto; /* Centra el div horizontalmente */
    background-color: #93d5b5;
    text-align: center;
    justify-self: center;
    align-items: center;
    cursor: pointer;
  }
}
.bienes {
  background-image: url(/build/img/proyectos/bienes/1.JPG);
  background-size: cover;
}

.salon {
  background-image: url(/build/img/proyectos/salon/1.PNG);
  background-size: cover;
}

.uptask {
  background-image: url(/build/img/proyectos/task/1.PNG);
  background-size: contain;
}
@media (min-width: 768px) {
  .uptask {
    background-size: cover;
  }
}

.bebidas {
  background-image: url(/build/img/proyectos/bebidas/1.jpeg);
  background-size: cover;
}

.capacidades {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.capacidades .tecnologias {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .capacidades .tecnologias {
    display: flex;
    flex-direction: column;
  }
}
.capacidades .tecnologias .grid-tecnologias {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}

@media (min-width: 768px) {
  .capacidades {
    display: flex;
    flex-direction: column;
    flex: auto;
    margin-bottom: 2rem;
  }
  .capacidades .tecnologias {
    display: flex;
    flex-direction: column;
  }
  .capacidades .tecnologias .grid-tecnologias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-capacidades {
    height: 100vh;
  }
  .contenedor-capacidades .capacidades {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    flex: auto;
    margin-bottom: 0;
    margin-right: 2rem;
  }
  .contenedor-capacidades .capacidades .tecnologias {
    display: flex;
    flex-direction: column;
  }
  .contenedor-capacidades .capacidades .tecnologias .grid-tecnologias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    max-width: 100%;
  }
}
.idiomas {
  display: flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}
.idiomas h4 {
  border-bottom: 1px solid #93d5b5;
}
.idiomas .contenedor-idiomas {
  display: flex;
  padding: 2rem;
  background-color: white;
  border: 1px solid #93d5b5;
  color: black;
}
.idiomas .contenedor-idiomas span {
  text-transform: uppercase;
  color: #18e985;
}
.idiomas .ingles {
  transition: all 0.2s ease-out;
}
.idiomas .ingles:hover {
  background-color: #93d5b5;
  color: white;
  border: 1px solid #18e985;
}
.idiomas .ingles:hover span {
  transition: all 0.2s ease-out;
  text-transform: uppercase;
  color: white;
}

.contenedor-tecnologias {
  background-color: #FFFFFF;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-out;
  z-index: 3;
  width: 100%;
  height: auto;
  max-height: 12rem;
  max-width: 12rem;
}
.contenedor-tecnologias img {
  background-color: transparent;
}
.contenedor-tecnologias p {
  display: none;
  cursor: default;
  position: absolute;
  opacity: 0;
}
.contenedor-tecnologias:hover {
  background-color: rgba(197, 196, 196, 0.431372549);
}
.contenedor-tecnologias:hover img {
  opacity: 0;
}
.contenedor-tecnologias:hover p {
  display: flex;
  text-align: center;
  justify-content: center;
  opacity: 1;
}

.star {
  position: absolute;
  width: 10px;
  height: 10px;
  background: radial-gradient(circle, transparent 1%, #93d5b5 100%);
  animation: twinkle 2s linear infinite;
  z-index: 1;
}

@keyframes twinkle {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(100%);
  }
}
.modal {
  display: none; /* Oculto por defecto */
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
  width: 100%;
  height: auto;
  max-height: 70rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .modal-content {
    width: 80%;
    height: auto;
  }
}

.box-close {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  padding-right: 8rem;
}

.close {
  color: #aaa;
  float: right;
  font-size: 8rem;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: rgb(251, 255, 0);
  text-decoration: none;
  cursor: pointer;
}

.modal-ingles {
  display: flex;
  flex-direction: column;
  width: 90%;
  height: 90%;
  overflow: auto;
  align-items: center;
  justify-content: center;
}
.modal-ingles img {
  width: 100%;
  height: auto;
  max-width: 200rem;
  max-height: 50rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .modal-ingles img {
    max-width: 200rem;
    max-height: 50rem;
  }
}

.carousel {
  position: relative;
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  max-height: 150rem;
}

.carousel img {
  width: 100%;
  height: auto;
  left: 0;
  position: sticky;
  object-fit: cover;
  border-radius: 1rem;
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .carousel img {
    object-fit: contain;
  }
}

.sub {
  cursor: pointer;
  margin-top: 2rem;
  padding-top: 1rem;
  max-width: 2rem;
  width: 100%;
  background-color: #817f7f;
}
.sub:hover {
  background-color: #555555;
}